import React, { lazy, useState } from "react";
import { Button, Card } from "antd";
import { RightPanelSizeButton } from "components/pages/Page";
import Loader from "../../utils/Loader";

const ScriptSettings = lazy(() => import("./ScriptSettings"));
const ScriptJobs = lazy(() => import("./ScriptJobs"));
const ScriptSchedules = lazy(() => import("./ScriptSchedules"));

export default function ScriptViews() {
  const [current, setCurrent] = useState("settings")
  const tabs = [
    {
      key: "settings",
      tab: "Settings",
    },
    {
      key: "jobs",
      tab: "Jobs",
    },
    {
      key: "schedules",
      tab: "Schedules",
    },
  ];

  const views = {
    settings: <ScriptSettings />,
    jobs: <ScriptJobs />,
    schedules: <ScriptSchedules />,
  };

  return (
    <Card
      bordered={false}
      headStyle={{ borderBottom: 0, height: 64 }}
      bodyStyle={{ paddingTop: 24 }}
      tabList={tabs}
      activeTabKey={current}
      onTabChange={(key) => setCurrent(key)}
      tabProps={{
        type: "line",
        tabBarStyle: { height: 64 },
      }}
      tabBarExtraContent={
        <RightPanelSizeButton>
          <Button />
        </RightPanelSizeButton>
      }
    >
      <Loader tip={`Loading ${current}`}>{views[current]}</Loader>
    </Card>
  );
}
